.root{
    display: flex;
    flex-direction: column;
  }

  .container:nth-of-type(1){
               
                display: flex;
                justify-content: center;
               flex-wrap: wrap;
               
        }

        .container::-webkit-scrollbar {
                            width: 0;
                        }
.movie-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .movie{
    background-color: #373b69;
    border-radius: 3px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    position: relative;
    margin: 1rem;
    width: 250px;
  }
  
  .movie img{
    object-fit: cover;
    height: 400px;
    width: 100%;
  }
  
  .movie-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    color: #fff;
  }
  
  .movie-info h3{
    margin: 0;
  }
  
  .movie-over{
    background-color: #fff;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.1);
    color: #22254b;
    padding: 1rem;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    max-height: 100%;
    transform: translateY(102%);/*pushed down 100%*/
    transition: transform 0.3s ease-in-out; 
  }
  
  .movie:hover .movie-over{
    transform: translateY(0%);/*appear*/
  }
  
  .tag{
    background-color: #22254b;
    border-radius: 5px;
    padding: 0.5rem;
    font-weight: bold;
  }
  
  .tag.green {
    color: rgb(5, 184, 5);
  }
  .tag.orange{
    color: orange;
  }
  .tag.red{
    color: red;
  }
  .error {
                    font-size: 38px;
                     color: red;
                    height: 32px;
               }
